import './App.css';
import {BrowserRouter, Route, Switch} from 'react-router-dom';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import '../node_modules/bootstrap/dist/js/bootstrap.bundle';
import About from './About';
import Experience from './Experience';
import Skills from './Skills';
import Projects from './Projects';

function App() {
  return (
    <>
      <BrowserRouter>
        <Switch>
          <Route path="/onlineresume/skills" component={Skills}/>
          <Route path="/onlineresume/projects" component={Projects}/>
          <Route path="/onlineresume/experience" component={Experience}/>
          <Route path="/onlineresume/" component={About}/>
        </Switch>
      </BrowserRouter>
    </>
  );
}

export default App;
