import React from 'react';
import {NavLink} from 'react-router-dom';

const Navbar = () => {
    return (
        <>
            <div className="custom_navbar">
                <NavLink exact activeClassName="active" to="/onlineresume/" className="custom_navbar_item text-center">About</NavLink>
                <NavLink exact activeClassName="active" to="/onlineresume/skills" className="custom_navbar_item text-center">Skills</NavLink>
                <NavLink exact activeClassName="active" to="/onlineresume/projects" className="custom_navbar_item text-center">Projects</NavLink>
                <NavLink exact activeClassName="active" to="/onlineresume/experience" className="custom_navbar_item text-center">Experience</NavLink>
            </div>
        </>
    )
}

export default Navbar;
